import {Component, Host, h, Prop, Watch, ComponentWillLoad} from '@stencil/core';
import {isDefined, isNotEmpty} from '../../utils/utils';
import {ProgressHelper, ProgressRange} from '../../services/ProgressHelper';
import {StatusHelper} from '../../services/StatusHelper';
import {SizeHelper} from '../../services/SizeHelper';

@Component({
  tag: 'soul-progress-ring',
  styleUrl: 'soul-progress-ring.scss',
  shadow: true,
})
export class SoulProgressRing implements ComponentWillLoad {

  @Prop({reflect: true}) size: 's' | 'm' | 'l' = 'm';
  @Prop({reflect: true}) indeterminate: boolean = false;
  @Prop({reflect: true}) progress = 0;
  @Prop({reflect: true}) min = 0;
  @Prop({reflect: true}) max = 100;
  @Prop({reflect: true}) label: string;
  @Prop({reflect: true}) accessibilityLabel: string;
  @Prop({reflect: true}) hideLabel: boolean = false;
  @Prop({reflect: true}) status: 'warning' | 'positive' | 'critical';
  @Prop({reflect: true}) color: 'neutral' | 'primary' = 'neutral';
  private supportedStatuses = ['positive', 'warning', 'critical'];
  private supportedSizes = ['s', 'm', 'l'];
  private progressHelper = new ProgressHelper();
  private statusHelper = new StatusHelper();
  private sizeHelper = new SizeHelper();
  private range: ProgressRange = {min: 0, max: 100};
  private radiusBySize: RadiusBySize = {s: '44%', m: '45%', l: '47%'};
  private readonly DEFAULT_SIZE = 'm';


  componentWillLoad(): Promise<void> | void {
    this.range = this.progressHelper.getRange(this.min, this.max);
  }

  @Watch('min')
  @Watch('max')
  rangeValueChange(): void {
    this.range = this.progressHelper.getRange(this.min, this.max);
  }

  @Watch('status')
  public statusChange(newValue: string): void {
    this.statusHelper.validateStatus(this.supportedStatuses, newValue);
  }

  @Watch('size')
  public sizeChange(newValue: string): void {
    this.sizeHelper.validateSize(this.supportedSizes, newValue);
  }

  render() {
    const progressPercentage = this.progressHelper.getProgressPercentage(this.range, this.progress);
    const label = this.progressHelper.getLabel(this.label, this.indeterminate, progressPercentage);
    const accessibilityLabel = this.progressHelper.getAccessibilityLabel(this.accessibilityLabel, label);
    const radiusBySize = isDefined(this.size) && this.sizeHelper.isValidSize(this.supportedSizes, this.size) ? this.radiusBySize[this.size] : this.radiusBySize[this.DEFAULT_SIZE];
    return (
      <Host role='progressbar' aria-valuemin={this.min} aria-valuemax={this.max} aria-valuenow={this.progress} aria-label={accessibilityLabel}
            title={this.hideLabel ? (accessibilityLabel ? accessibilityLabel : label) : ''}>
        <div class={{
          'soul-progress-ring': true,
          [this.indeterminate ? 'soul-progress-ring--indeterminate' : `soul-progress-ring--progress-${progressPercentage}`]: true,
          [isDefined(this.size) && this.sizeHelper.isValidSize(this.supportedSizes, this.size) ? `soul-progress-ring--${this.size}` : 'soul-progress-ring--m']: true,
          'soul-progress-ring--critical': this.status === 'critical',
          'soul-progress-ring--warning': this.status === 'warning',
          'soul-progress-ring--positive': this.status === 'positive'
        }}>
          <svg class=' soul-progress-ring__circles' width='100%' height='100%'>
            <circle class='soul-progress-ring__track' cx='50%' cy='50%' r={radiusBySize}/>
            <circle class='soul-progress-ring__fill' cx='50%' cy='50%' r={radiusBySize}/>
          </svg>
          {this.hasLabel(label) ? <span class='soul-progress-ring__content'>{label}</span> : ''}
          {isDefined(this.status) && this.statusHelper.isValidStatus(this.supportedStatuses, this.status) ? <div class='soul-progress-ring__indicator'>
            <soul-indicator type={this.status} border></soul-indicator>
          </div> : ''
          }
        </div>
      </Host>
    );
  }

  private hasLabel(label: string): boolean {
    return !this.hideLabel && isNotEmpty(label);
  }


}

type RadiusBySize = { l: string, s: string, m: string };
