@use "modules/core";
@use 'modules/essentials/helpers';
@use 'modules/components/label';
@use "modules/components/progress-ring";

$_indicator-position: ("bottom": 20%, "right": 14%);
$_indicator_size: 40%;

:host {
  display: block;
}


:host,
:host([color='neutral']) {
  --soul-progress-ring-fill-color: #{core.$soul-color-background-interactive-default-selected};
}

:host([color='primary']) {
  --soul-progress-ring-fill-color: #{core.$soul-color-background-status-brand-primary};
}

.soul-progress-ring--warning {
  --soul-progress-bar-fill-color: #{core.$soul-color-background-status-warning};
  color: core.$soul-color-text-indicator-warning;
}

.soul-progress-ring--critical {
  --soul-progress-bar-fill-color: #{core.$soul-color-background-status-critical};
  color: core.$soul-color-text-indicator-negative;
}

.soul-progress-ring--positive {
  --soul-progress-bar-fill-color: #{core.$soul-color-background-status-positive};
  color: core.$soul-color-text-indicator-positive;
}

.soul-progress-ring__indicator {
  position: absolute;
  bottom: map-get($_indicator-position, "bottom");
  right: map-get($_indicator-position, "right");
  transform: translate(50%, 50%);
  width: var(--_soul-progress-ring-indicator-size, $_indicator_size);
  height: var(--_soul-progress-ring-indicator-size, $_indicator_size);
}

:host([size='s']) {
  --_soul-progress-ring-indicator-size: 70%;
}

soul-indicator {
  --soul-indicator-size: 100%;
}

.soul-progress-ring__content {
  overflow: hidden;
}
